import { defineStore } from "pinia";
import {
  createPointsWithIds,
  getPoints,
  uploadFiscalKey,
} from "../api/points.api";
import { AddPointsRequest, PointsRequest } from "../model/points-request";

import router from "@/app/router";
import { Point, PointRecord } from "../model/point";

export const usePointsStore = defineStore("points", {
  state: () => ({
    loading: false,
    pointsByKey: [] as Point[],
    points: [] as PointRecord[],
  }),

  actions: {
    async getPoints() {
      this.loading = true;
      const reponse = await getPoints();

      if (reponse.code === 0) {
        this.points = reponse.data?.map((point) => ({
          ...point,
          is_fiscal: point.is_fiscal
            ? JSON.parse(point.is_fiscal)
              ? "Так"
              : "Ні"
            : "",
          is_test: point.is_test
            ? JSON.parse(point.is_test)
              ? "Так"
              : "Ні"
            : "",
          subRows: [
            {
              ID: point.id,
              Назва: point.POINT_NAME,
              Адреса: point.POINT_ADDRESS,
            },
          ],
        }));
      }

      this.loading = false;
    },

    async uploadFiscalKey(request: PointsRequest) {
      const reponse = await uploadFiscalKey(request);

      if (reponse.code === 0) {
        this.pointsByKey = reponse.data;
        router.push({ name: "fiscalAdd" });
      }
    },

    async craetePoints(request: AddPointsRequest) {
      const reponse = await createPointsWithIds(request);

      if (reponse.code === 0) {
        this.pointsByKey = reponse.data;
      }
    },
  },
});
