import { http } from "@/api/client";
import { AddPointsRequest, PointsRequest } from "../model/points-request";
import { ApiRoutes } from "@/api/routes";
import { sign, signArray } from "@/api/crypto/DeffiHellman";
import { getToken } from "@/api/crypto/token";

export const getPoints = async () => {
  const { data } = await http.get(ApiRoutes.GET_POINTS);

  return data;
};

export const uploadFiscalKey = async (request: PointsRequest) => {
  const signedPayload = {
    ...request,
    sign: await sign(
      { fiscal_name: request.fiscal_name, fiscal_pass: request.fiscal_pass },
      getToken()
    ),
  };

  const { data } = await http.postForm(
    ApiRoutes.UPLOAD_FISCAL_KEY,
    signedPayload
  );

  return data;
};

export const createPointsWithIds = async (request: AddPointsRequest) => {
  const payloadForSign = request.points.map((point) => ({
    chief_of_point: point.chief_of_point,
    chief_phone: point.chief_phone,
    close_day: point.close_day,
    close_time: point.close_time,
  }));

  const signedPayload = {
    ...request,
    sign: await signArray(payloadForSign, getToken()),
  };

  const { data } = await http.post(ApiRoutes.ADD_POINT, signedPayload);

  return data;
};
